import React from "react";
import {
  Grid,
  Box,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Pagination,
  PaginationItem,
} from "@mui/material";
import { useEffect, useState, useRef } from "react";

import ItemCard from "../ItemCard/ItemCard";
import { useParams, Link } from "react-router-dom";
function PostList() {
  const page = useParams("pageNo")["pageNo"];

  const [posts, setPosts] = useState([]);
  const mounted = useRef(false);
  useEffect(() => {
    mounted.current = true;
    console.log(page);
    fetch("/posts/" + (page === undefined ? "1" : page) + ".json", {
      headers: {
        "Content-Type": "application/json",

        Accept: "application/json",
      },
    })
      .then((data) => data.json())
      .then((data) => {
        if (mounted.current) {
          console.log(data);
          setPosts(data.posts);
        }
      });
    return () => {
      mounted.current = false;
    };
  }, [page]);

  console.log(posts);

  return (
    <Grid container spacing={2}>
      {posts.map((post) => (
        <Grid key={post.id} item xs={12} md={6}>
          <ItemCard item={post} />
        </Grid>
      ))}
      <Grid item xs={12}>
        <Pagination
          sx={{
            marginTop: "8%",
            marginBottom: "8%",

            display: "flex",
            justifyContent: "center",
          }}
          page={parseInt(page || "1", 10)}
          count={10}
          variant="outlined"
          shape="rounded"
          renderItem={(item) => (
            <PaginationItem
              component={Link}
              to={`/${item.page === 0 ? "" : `page/${item.page}`}`}
              {...item}
            />
          )}
        />
      </Grid>
    </Grid>
  );
}

export default PostList;
